<template>
  <v-app :style="{ background: $vuetify.theme.themes.dark.background}">
    <SideBar/>
    <TopBar/>
    <v-container  fluid style="background-color: #323B50" >
      <v-card color="#323B50" class="my-2 card_global flex-grow-0">
        <h2 class="mt-2" >Branch Dispatch</h2>
        <!--p class="pl-4">No Credit transactions.</p-->
        <v-row >
          <v-col cols="12" sm="8" >
            <v-card flat class="card_item pa-2" color="#323B50">
              <!--v-virtual-scroll
                :items="Array.from({length: 1}).map((_, index) => index)"
                :item-height="250"
                height="650"
              -->
              <v-list color="#323B50" style="height: 570px; overflow-y: scroll; overflow-x: hidden">
              <v-row>
                 <v-col cols="12" sm="3" v-for="(product, i) in productInventory" :key="i">
                  <v-hover v-slot="{hover}" close-delay="200">
                    <v-card :elevation="hover ? 16 :2" :class="{ 'on-hover' : hover}"
                    class="mx-auto align-center" height="250" tile color="#333c51">
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn tile small :color="hover ? '#F34F64' : '#777D8B'" dark>
                          {{product.price}}
                        </v-btn>
                      </v-card-actions>
                      <v-img :src="product.img" width="150" height="150"></v-img>
                      <v-card-text :class="hover ? 'red--text' : 'white--text'">
                        {{product.title}}
                      </v-card-text>
                    </v-card>
                  </v-hover>
                 </v-col>
              </v-row>
            </v-list>
            <!--/v-virtual-scroll-->
            </v-card>
            <v-list subheader two-line color="white">
              <v-list-item >
                <v-list-item-avatar size="50px">
                  <v-icon class="blue-grey darken-2" color="white" dark >mdi-truck</v-icon>
                </v-list-item-avatar>
                <v-list-item-content >
                  <v-list-item-title class="white--text"></v-list-item-title>
                  <v-list-item-subtitle class="grey--text mt-2"><v-combobox class="pt-2" v-model="Branches" :items="branch" label="Truck Number" clearable  dense></v-combobox></v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-col>
          <v-col cols="12" sm="4">
            <Cart/>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
  </v-app>
</template>

<script>
  import Cart from "../components/Cart";
  import SideBar from "../components/Sidebar";
  import TopBar from "../components/Topbar";
  export default {
    components: {Cart, SideBar, TopBar},
    data: () => ({
      bpm: 1,
      productInventory: [
        
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
        {img: "https://cdn.vuetifyjs.com/images/cards/plane.jpg", title: "Passion Juice", subtitle: "500ml", price: "Ksh 200"},
      ],
      cards: [
        { title: 'Favorite road trips', src: './assets/images/tropical lush.png', flex: 3, hprice: 'Ksh. 140.00' },
        { title: 'Best airlines', src: 'https://cdn.vuetifyjs.com/images/cards/plane.jpg', flex: 3, hprice: 'Ksh. 140.00'  },
        { title: 'Favorite road trips', src: './assets/images/tropical lush.png', flex: 3, hprice: 'Ksh. 140.00' },
        { title: 'Best airlines', src: 'https://cdn.vuetifyjs.com/images/cards/plane.jpg', flex: 3, hprice: 'Ksh. 140.00'  },
        { title: 'Favorite road trips', src: './assets/images/tropical lush.png', flex: 3, hprice: 'Ksh. 140.00' },
        { title: 'Best airlines', src: 'https://cdn.vuetifyjs.com/images/cards/plane.jpg', flex: 3, hprice: 'Ksh. 140.00'  },

      ],
      drawer: null,
      dialog: false,
      branch: [
          'KCT 098T',
          'KBY 234R',
          'KCS 565Y',
          'KCZ 276F',
        ],
          dialogDelete: false,
          headers: [
            {
              text: 'Product Name',
              align: 'start',
              sortable: false,
              value: 'name',
            },
            { text: 'Price/Unit', value: 'unitPrice' },
            { text: 'Cases', value: 'cases' },
            { text: 'Total Units', value: 'totalunits' },
            { text: 'Total Cost', value: 'totalcost' },
            { text: 'Actions', value: 'actions', sortable: false },
          ],
          products: [],
          defaultItem: {
            name: '',
            unitPrice: 0,
            cases: 0,
            totalunits: 0,
            totalcost: 0,
          },
    }),
    computed: {
          formTitle () {
            return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
          },
        },
    
        watch: {
          dialog (val) {
            val || this.close()
          },
          dialogDelete (val) {
            val || this.closeDelete()
          },
        },
    
        created () {
          this.initialize()
        },
    
        methods: {
          decrement() {
            this.bpm--
          },
          increment () {
            this.bpm++
          },
          initialize () {
            this.products = [
              {
                name: '500ml passion juice',
                unitPrice: 250.00,
                cases: 10,
                totalunits: 120,
                totalcost: 2500.00,
              },
              {
                name: '500ml mango juice',
                unitPrice: 250.00,
                cases: 20,
                totalunits: 240,
                totalcost: 5000.00,
              },
              {
                name: '1Litre passion juice',
                unitPrice: 500.00,
                cases: 10,
                totalunits: 60,
                totalcost: 5000.00,
              },
              {
                name: '1Litre pineapple juice',
                unitPrice: 500.00,
                cases: 20,
                totalunits: 120,
                totalcost: 5000.00,
              },
            ]
          },
    
          editItem (item) {
            this.editedIndex = this.desserts.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
          },
    
          deleteItem (item) {
            this.editedIndex = this.desserts.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialogDelete = true
          },
    
          deleteItemConfirm () {
            this.desserts.splice(this.editedIndex, 1)
            this.closeDelete()
          },
    
          close () {
            this.dialog = false
            this.$nextTick(() => {
              this.editedItem = Object.assign({}, this.defaultItem)
              this.editedIndex = -1
            })
          },
    
          closeDelete () {
            this.dialogDelete = false
            this.$nextTick(() => {
              this.editedItem = Object.assign({}, this.defaultItem)
              this.editedIndex = -1
            })
          },
    
          save () {
            if (this.editedIndex > -1) {
              Object.assign(this.desserts[this.editedIndex], this.editedItem)
            } else {
              this.desserts.push(this.editedItem)
            }
            this.close()
          },
        },
  }
</script>
<style>
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #fff;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #555;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  h2 {
    color: #fff !important;
    cursor: pointer !important;
  }
  p{
    color: #fff;
    cursor: pointer;
  }


</style>